<template>
  <transition name="fade" mode="in-out">
    <div class='personalHomePage'>
      <v-Header class="head">
        <span slot='text'>个人主页</span>
      </v-Header>
      <div class='personalHomePageScroll'>
        <div class="bg">
          <div class="mask">
            <div class="userInfo">
              <div class="header" :style="{backgroundImage:'url('+headimg+')'}"></div>
              <span class="name" v-text='nickname'></span>
            </div>
          </div>
        </div>
        <div class="listCon">
          <div @click='showPage("Message")'>
            <mt-cell class="list" title="我的信息" is-link></mt-cell>
          </div>
          <div @click='showPage("MyForum")'>
            <mt-cell class="list" title="我的发帖" is-link></mt-cell>
          </div>
          <div @click='showPage("Collection")'>
            <mt-cell class="list" title="我感兴趣的" is-link></mt-cell>
          </div>
          <div @click='showPage("Draft")'>
            <mt-cell class="list" title="我的草稿" is-link></mt-cell>
          </div>
          <div @click='showPage("PublishInfo",3)'>
            <mt-cell class="list" v-if='userType!==1' title="我发布的行程" is-link>
              <img slot="icon" src="../../assets/images/logo.png" style="width:0.26rem;height:0.32rem">
            </mt-cell>
          </div>
          <div @click='showPage("PublishInfo",5)'>
            <mt-cell class="list" v-if='userType!==1' title="我发布的课程" is-link>
              <img slot="icon" src="../../assets/images/logo.png" style="width:0.26rem;height:0.32rem">
            </mt-cell>
          </div>
          <div @click='showPage("PublishInfo",4)'>
            <mt-cell class="list" v-if='userType!==1' title="我发布的装备" is-link>
              <img slot="icon" src="../../assets/images/logo.png" style="width:0.26rem;height:0.32rem">
            </mt-cell>
          </div>
          <hr v-if='userType!==3'>
          <div @click='showPage("expert")'>
            <mt-cell class="list" title="达人资料管理" v-if='userType==2||(userType==1&&auditUserType==2)||(userType==1&&!auditUserType)' is-link>
              <span class="rText">{{this.auditUserType?'审核中':(userType==2?'管理达人资料':'未认证达人')}}</span>
              <img slot="icon" src="../../assets/images/logo.png" style="width:0.26rem;height:0.32rem">
            </mt-cell>
          </div>
          <hr v-if='userType!==2'>
          <div @click='showPage("coach")'>
            <mt-cell class="list" title="教练资料管理" v-if='userType==3||(userType==1&&auditUserType==3)||(userType==1&&!auditUserType)' is-link>
              <span class="rText">{{this.auditUserType?'审核中':(this.userType==3?'管理教练资料':'未认证教练')}}</span>
              <img slot="icon" src="../../assets/images/logo.png" style="width:0.26rem;height:0.32rem">
            </mt-cell>
          </div>
          <hr >
          <div @click='showPage("myClub")'>
            <mt-cell class="list" title="我的俱乐部" is-link>
              <img slot="icon" src="../../assets/images/clubLogo.png" style="width:0.35rem;height:0.3rem">
            </mt-cell>
          </div>
        </div>
      </div>
      <router-view/>
    </div>
  </transition>
</template>
<script>
  import Header from '../../components/header'

  export default {
    name: '',
    components: {'v-Header': Header},
    data () {
      return {
        userType: 1,
        auditUserType: '',
        nickname: '',
        headimg: '',
        userId:'',
        bgImg: 'https://s33.postimg.cc/acjanzcm7/image.jpg'
      }
    },
    methods: {
      showPage (type, info) {
        switch (type) {
          case 'Draft':
            this.$router.push({path: `/personalHomePage/draft`})
            break
          case 'Message':
            this.$router.push({path: `/personalHomePage/msg`})
            break
          case 'Collection':
            this.$router.push({path: `/personalHomePage/collectionPage`})
            break
          case 'MyForum':
            this.$router.push({path: `/personalHomePage/forumPage`})
            break
          case 'PublishInfo':
            this.$router.push({path: '/personalHomePage/publishInfo', query: {publishInfoType: info}})
            break
          case 'expert':
            this.$router.push({path: '/auth/expert',query:{expertId:this.userId}})
            break
          case 'coach':
            this.$router.push({path: '/auth/coach',query:{coachId:this.userId}})
            break
          case 'myClub':
            this.$router.push({path: `/personalHomePage/myClub`,query:{clubId:this.userId}})
            break
          default:
            break
        }
      },
      getUserInfo () {
        this.$http.get(this.$port.MY_PROT, {}).then((response) => {
          const {data} = response
          if (response.code == 200) {
            this.nickname = data.nickname
            this.headimg = data.headimg
            this.userType = data.userType
            this.auditUserType = data.auditUserType
            this.userId = data.userId
          }
        })
          .catch((error) => {
            console.log(error.response.data)
          })
      },
    },
    mounted () {
      this.getUserInfo()
    }
  }
</script>
<style lang="scss">
  .list {
    .mint-cell-wrapper {
      padding: 0;
      background-image: linear-gradient(180deg, #f2f2f2, #f2f2f2 50%, transparent 50%);
    }
  }
</style>

<style lang="scss" scoped>
  * {
    border: none;
    margin: 0;
    padding: 0;
  }

  .page {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
  }

  .personalHomePage {
    @extend .page;
    overflow: hidden;
    .personalHomePageScroll {
      position: absolute;
      top: 0.82rem;
      left: 0;
      right: 0;
      bottom: 0;
      margin: 0;
      overflow-x: hidden;
      overflow-y: scroll;
      -webkit-overflow-scrolling: touch;
      background: #f2f2f2;
      .bg {
        width: 7.5rem;
        height: 4.1rem;
        background:url(../../assets/images/1.jpg);
        background-size: cover;
        background-position: center;
        position: relative;
        border-bottom: 0.2rem solid #f2f2f2;
        .mask {
          width: 7.5rem;
          height: 4.1rem;
          position: absolute;
          top: 0;
          left: 0;
          background: rgba(0, 0, 0, 0.7);
          display: flex;
          align-items: center;
          justify-content: center;
          .userInfo {
            display: flex;
            flex-direction: column;
            align-items: center;
            .header {
              width: 1.2rem;
              height: 1.2rem;
              background-size: cover;
              background-position: center;
              border-radius: 1.2rem;
            }
            .name {
              margin-top: 0.26rem;
              font-size: 0.32rem;
              font-weight: bold;
              color: white;
            }
          }
        }
      }
      .listCon {
        .list {
          height: 0.88rem;
          line-height: 0.88rem;
          font-size: 0.32rem;
          color: #888888;
          text-indent: 0.3rem;
          .rText {
            margin-right: 0.17rem;
          }
        }
        hr {
          width: 100%;
          height: 0.2rem;
          background: #f2f2f2;
        }
      }
    }
  }
</style>





